@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Raleway:300|Muli&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }
  .App .landing {
    margin-top: 200px; }

.searchForm {
  padding: 30px 30px;
  display: flex;
  justify-content: center;
  background-color: inherit; }
  .searchForm .searchInput {
    font-family: 'Muli', sans-serif;
    line-height: 1.2;
    background-color: inherit;
    color: white;
    outline: none;
    border: none;
    border-bottom: solid 1px #36daff;
    font-size: 1.5em;
    padding: 5px 8px; }
    .searchForm .searchInput::-webkit-input-placeholder {
      background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::-moz-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput:-ms-input-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::-ms-input-placeholder {
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .searchForm .searchInput::placeholder {
      background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
      background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
  .searchForm .searchButton {
    background-color: inherit;
    border: 0;
    outline: 0; }
    .searchForm .searchButton .searchIcon {
      width: 25px;
      height: 25px; }

.searchResults {
  padding: 20px 50px;
  text-align: left;
  line-height: 2; }
  .searchResults .resultTitle {
    font-size: 2em;
    background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
    background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .searchResults .titleUnderline {
    background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
    background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
    width: 200px;
    height: 5px;
    margin: 5px 0 20px 0; }

.errorMessage {
  background: -webkit-gradient(linear, left top, right top, from(#ff2f60), to(#ff49bd));
  background: linear-gradient(to right, #ff2f60 0%, #ff49bd 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2em; }

@media (max-width: 425px) {
  .searchForm {
    position: absolute;
    z-index: 3;
    bottom: 30px; } }

